import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function cik(_tableName: string) {
  const title = "CIK"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["cik"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The company's SEC ID number.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyBalanceStatement?.data?.cik}
          queryResult={info.row.original.financialData.companyBalanceStatement}
        >
          {info.row.original.financialData.companyBalanceStatement?.data?.cik}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { cik }
